import React, { useState } from "react";
import { useDispatch, useSelector } from 'react-redux';
import Button from '@mui/material/Button';
import { Breadcrumb, SimpleCard } from "app/components";
import { MCOService } from 'services/mcoService';
import { getMCOLists, update_generalValues } from 'app/redux/actions/MCODetailsAction';
import MCOListTable from "app/components/McoPages/MCOListTable";
import Loader from 'app/components/Loader';
import CustomAlertBox from "app/components/McoPages/CustomAlertBox";
import SomethingWrong from "app/components/McoPages/PageError";
import {
    Box,
    styled,
    Grid,
    TextField,
    InputAdornment
} from "@mui/material";
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import "./mcoStyles.css";
import { exportTableData } from "app/utils/mcoUtils/caDetailsUtils";

const Container = styled("div")(({ theme }) => ({
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
        marginBottom: "30px",
        [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
}));

const McoList = () => {
    const dispatch = useDispatch();
    const { mcolist: mcolistsArr } = useSelector((state) => state.mcoDetails);
    const [search, setSearch] = useState({ type: "MCO", limit: '500' });
    const [isLoading, setIsLoading] = useState(false);
    const [isHavingError, setIsHavingError] = useState(false);
    const [errorDetails, setErrorDetails] = useState(null);
    const totalLimits = 500;
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const mcoService = new MCOService();
    const handleChange = (event) => {
        event.persist();
        const searchArr = { ...search, searchPattern: event.target.value };
        setSearch(searchArr);
    };
    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            const searchArr = { ...search, searchPattern: event.target.value };
            setSearch(searchArr);
            callSearchApi(searchArr);
        }
    };
    const callSearchApi = async (searchValues) => {
        try {
            // using api filter
            setOpenAlert(false);
            setIsLoading(true);
            setErrorDetails(null);
            const newsearchValues = { ...searchValues, searchPattern: searchValues.searchPattern + "" };
            await mcoService.searchMCO(newsearchValues).then(async (result) => {
                if (Array.isArray(result)) {
                    if (result.length) {
                        dispatch(getMCOLists({ mcolist: result }));
                        const mcoTitles = result.map(mitem => mitem?.title).filter(title => title);
                        const baseMcoTitle = searchValues.searchPattern;
                        const filteredMcoTitles = mcoTitles.filter(title => title.startsWith(baseMcoTitle));
                        await mcoService.getMcoDetailsByName(filteredMcoTitles).then((result) => {
                            if (Object.keys(result).length) {
                                dispatch(update_generalValues({ mcoSearchLists: result }));
                            }
                        });
                        if (result.length >= totalLimits) {
                            setErrorDetails(`Please refine your search criteria, as the maximum limit of ${totalLimits} search results has been exceeded.`);
                        }
                    } else {
                        dispatch(getMCOLists({ mcolist: [] }));
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "error",
                            desc: "No result found!",
                        });
                    }
                } else {
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: "error",
                        desc: result,
                    });
                }
                setIsLoading(false);
            }).catch((err) => {
                setIsLoading(false);
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: "Something went wrong!",
                });
                return err;
            })
        } catch (err) {
            setIsHavingError(true);
        }
    }

    return (
        <Container>
            {
                (!isHavingError) ? (
                    <>
                        <Box className="breadcrumb">
                            <Breadcrumb routeSegments={[{ name: "Search MCO" }]} />
                        </Box>
                        <SimpleCard>
                            {
                                (errorDetails) && (
                                    <Stack sx={{ width: '100%', marginBottom: "25px" }} spacing={2}>
                                        <Alert severity="info" onClose={() => { setErrorDetails(null) }}>
                                            {errorDetails}
                                        </Alert>
                                    </Stack>
                                )
                            }
                            <Grid
                                container
                                spacing={1}
                                alignItems="center"
                            >
                                <Grid item md={3} sm={6} xs={12} className="font-medium">
                                    <TextField
                                        type="text"
                                        name="search"
                                        size="small"
                                        onChange={handleChange}
                                        onKeyDown={handleKeyDown}
                                        value={search?.searchPattern || ""}
                                        style={{ width: '100%' }}
                                        InputProps={{
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                    *
                                                </InputAdornment>
                                            ),
                                        }}
                                    />
                                </Grid>
                                <Grid item md={9} sm={6} xs={12}>
                                    <Button variant="contained" onClick={() => { callSearchApi(search) }}>Search</Button>

                                    <Button variant="contained" onClick={() => { exportTableData(mcolistsArr, "searchMCO") }} style={{ float: "right", marginRight: "15px" }}>Export</Button>
                                </Grid>
                                <Grid item md={12} sm={12} xs={12}>
                                    <MCOListTable mcolistsArr={mcolistsArr || []} />
                                </Grid>
                            </Grid>

                            {(openDesc) ? (
                                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
                            ) : (<></>)}
                            {isLoading ? (
                                <div style={{ textAlign: 'center' }}>
                                    <Loader show={isLoading} />
                                </div>
                            ) : <></>}
                        </SimpleCard>
                    </>
                ) : (
                    <SomethingWrong />
                )
            }
        </Container >
    );
};

export default McoList;