import React, { useEffect, useState } from 'react';
import {
  Box,
  styled,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
} from "@mui/material";
import { Link } from 'react-router-dom';
import shortId from 'shortid';
import { useSelector, shallowEqual } from 'react-redux';
import IconButton from '@mui/material/IconButton';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import ErpStatusReasonDialogBox from './ErpStatusReasonDialogBox';

const StyledTable = styled(Table)(() => ({
  whiteSpace: "pre",
  "& thead": {
    "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
  },
  "& tbody": {
    "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
  },
}));

// const displayERPValue = (val) => {
//   if (Object.keys(val)?.length) {
//     return val?.ERPstatus ?? "-";
//   } else {
//     return "-";
//   }
// }

const displayERPValue = (val) => {
  if (!val || typeof val !== 'object') {  // ✅ Ensure val is a valid object
      return "-";
  }
  return val?.ERPstatus ?? "-";
};
const MCOListTable = (props) => {
  const {
    mcoSearchLists
  } = useSelector((state) => ({
    mcoSearchLists: state.mcoDetails.mcoSearchLists || {},
  }), shallowEqual);
  let { mcolistsArr } = props;
  const instance = localStorage.getItem('instance')
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(500);
  const [mcolists, setMcolists] = useState(mcolistsArr || []);

  const [erpStatusReasonDialogBox, setErpStatusReasonDialogBox] = useState(false);
  const [selectedMCO, setSelectedMCO] = useState(null);
  const handleERPStatusReason = () => {
    setErpStatusReasonDialogBox(true);
  }
  const updateERPStatusReason = () => {
    setErpStatusReasonDialogBox(false);
  }

  const handleChangePage = (_, newPage) => {
    setPage(newPage);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  useEffect(() => {
    const mcoFilterList = (mcolistsArr?.length) ? (mcolistsArr.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)) : [];
    setMcolists(mcoFilterList);

  }, [page, rowsPerPage, mcolistsArr])

  return (
    <Box width="100%" overflow="auto">
      <StyledTable>
        <TableHead>
          <TableRow>
            <TableCell align="left" style={{ width: "10%", wordWrap: "word-break" }}>MCO Name</TableCell>
            <TableCell align="left" style={{ width: "10%", wordWrap: "word-break" }}>RDO</TableCell>
            <TableCell align="left" style={{ width: "5%", wordWrap: "word-break" }}>State</TableCell>
            <TableCell align="left" style={{ width: "5%", wordWrap: "word-break" }}>Plant</TableCell>
            <TableCell align="left" style={{ wordWrap: "word-break" }}>Start Date</TableCell>
            <TableCell align="center" style={{ width: "10%", wordWrap: "word-break" }}>Export Status</TableCell>
            <TableCell align="center" style={{ wordWrap: "word-break" }}>Failed Integration Step</TableCell>
            <TableCell align="left" style={{ width: "10%", wordWrap: "word-break" }}>Resp. Mfg Eng.</TableCell>
            <TableCell align="left" style={{ wordWrap: "word-break" }}>Resp. Senior Mfg Eng.</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
              {mcolists?.length ? (
                mcolists
                  .filter(subscriber => {
                    const subscriberTitle = subscriber?.title?.trim().toUpperCase();
                    if (!subscriberTitle) return false; // Skip invalid entries
                    const mcoTitles = Object.keys(mcoSearchLists || {}).map(title => title.trim().toUpperCase());
                    return mcoTitles.includes(subscriberTitle);
                  })
                  .map(subscriber => {
                    const subscriberTitle = subscriber?.title?.trim().toUpperCase();
                    const validMCOName = Object.keys(mcoSearchLists || {}).find(mcoTitle =>
                      mcoTitle.trim().toUpperCase() === subscriberTitle
                    );

                    const currentMCODetails = validMCOName ? mcoSearchLists[validMCOName] : {};
                return (
                  <TableRow key={shortId.generate()} className='dataTableMCO'>
                    <TableCell align="left">
                      <Link to={'/mcodetail/' + (instance === 'SaaS' ? subscriber?.id : validMCOName)} style={{ color: "#1976d2" }} >
                        {validMCOName}
                      </Link>
                    </TableCell>
                    <TableCell align="left">{subscriber?.businessUnit || "-"}</TableCell> 
                    <TableCell align="left">{subscriber?.mcostate || "-"}</TableCell> 
                    <TableCell align="left">{subscriber?.plantName || "-"}</TableCell> 
                    <TableCell align="left">{subscriber?.startdate || "-"}</TableCell> 
                    <TableCell align="center">{displayERPValue(currentMCODetails)}</TableCell>
                    <TableCell align="center">
                      {
                        (currentMCODetails?.ERPstatus) ? (
                          <IconButton
                            aria-label="close"
                            onClick={(e) => {
                              e.stopPropagation();
                              setSelectedMCO(currentMCODetails);
                              handleERPStatusReason();
                            }}
                            sx={(theme) => ({
                              position: 'relative',
                              right: 0,
                              top: 0,
                              color: (currentMCODetails?.ERPstatus?.toLowerCase() === "success" ? "limegreen" : "red") ?? "#1976d2",
                            })}
                          >
                            <VisibilityIcon />
                          </IconButton>
                        ) : (
                          <IconButton
                            aria-label="close"
                            onClick={(e) => {
                              e.stopPropagation();
                            }}
                            sx={(theme) => ({
                              position: 'relative',
                              right: 0,
                              top: 0,
                              color: theme.palette.grey[500],
                            })}
                          >
                            <VisibilityOffIcon />
                          </IconButton>
                        )
                      }

                    </TableCell>
                    <TableCell align="left">{subscriber?.originator }</TableCell>
                    <TableCell align="left">{subscriber?.originator }</TableCell>
                  </TableRow>
                )
              })) : (
              <TableRow key={shortId.generate()}>
                <TableCell align="left" colSpan={9} style={{ textAlign: 'center' }}>{'No result found!'}</TableCell>
              </TableRow>
            )
          }
        </TableBody>
      </StyledTable>
      {
        (mcolistsArr?.length) ? (
          <TablePagination
            sx={{ px: 2 }}
            page={page}
            component="div"
            rowsPerPage={rowsPerPage}
            count={mcolistsArr?.length}
            onPageChange={handleChangePage}
            rowsPerPageOptions={[100, 200, 300, 400, 500]}
            onRowsPerPageChange={handleChangeRowsPerPage}
            nextIconButtonProps={{ "aria-label": "Next Page" }}
            backIconButtonProps={{ "aria-label": "Previous Page" }}
          />
        ) : (<></>)
      }

      {
        (erpStatusReasonDialogBox) ? (
          <>
            <ErpStatusReasonDialogBox
              erpStatusReasonDialogBox={erpStatusReasonDialogBox}
              updateERPStatusReason={updateERPStatusReason}
              selectedMCO={selectedMCO}
              setSelectedMCO={setSelectedMCO}
            />
          </>
        ) : (null)
      }
    </Box>
  );
};

export default MCOListTable;
